import Vue from 'vue'
import App from '../App'
import Router from 'vue-router'
import myApp from '@/components/myApp'
//
import system from '@/components/system/system'
import login from '@/components/system/login'
//
import systemManage from '@/components/systemManage/systemManage'
import userManage from '@/components/systemManage/userManage'
import sysManage from '@/components/systemManage/sysManage'
import operateLog from '@/components/systemManage/operateLog'
import operateLogOptLog from '@/components/systemManage/operateLogOptLog'
import operateLogCtrlLog from '@/components/systemManage/operateLogCtrlLog'
//
import statisticalStatement from '@/components/statisticalStatement/statisticalStatement'
import batteryMsg from '@/components/statisticalStatement/batteryMsg'
import batteryGroupMsg from '@/components/statisticalStatement/batteryGroupMsg'
import problemBattery from '@/components/statisticalStatement/problemBattery'

//
import applicationPlatform from '@/components/applicationPlatform/applicationPlatform'

import batteryMonitorSys from '@/components/applicationPlatform/batteryMonitorSys'

import zonesEdit from '@/components/applicationPlatform/zonesEdit' 
import collectorsEdit from '@/components/applicationPlatform/collectorsEdit' //
import collectorMsgContainer from '@/components/applicationPlatform/collectorMsg/collectorMsgContainer'
import bianyi from '@/components/applicationPlatform/collectorMsg/bianyi' //
import systemDiagram from '@/components/applicationPlatform/collectorMsg/systemDiagram' //
import fileManage from '@/components/applicationPlatform/collectorMsg/fileManage' //
import dataAnalysis from '@/components/applicationPlatform/collectorMsg/dataAnalysis' //
import runStatus from '@/components/applicationPlatform/collectorMsg/runStatus/runStatus' //

import runStatusStringValue from '@/components/applicationPlatform/collectorMsg/runStatus/runStatus_stringValue' //--
import runStatusSampleValue from '@/components/applicationPlatform/collectorMsg/runStatus/runStatus_sampleValue' //--
import runStatusOutputValue from '@/components/applicationPlatform/collectorMsg/runStatus/runStatus_outputValue' //--
import runStatusInsulationMonitoring from '@/components/applicationPlatform/collectorMsg/runStatus/runStatus_insulationMonitoring' //--
import runStatusChargeMachine from '@/components/applicationPlatform/collectorMsg/runStatus/runStatus_chargeMachine' //--
import runStatusPowerStatus from '@/components/applicationPlatform/collectorMsg/runStatus/runStatus_powerStatus' //--


import alarmAnalysis from '@/components/applicationPlatform/collectorMsg/alarmAnalysis' //
import constantManage from '@/components/applicationPlatform/collectorMsg/constantManage/constantManage' //
import constantStringValue from '@/components/applicationPlatform/collectorMsg/constantManage/constantStringValue' //--modbus
//import constantChargeDevice from '@/components/applicationPlatform/collectorMsg/constantManage/constantChargeDevice' //--
import constantDischargeDevice from '@/components/applicationPlatform/collectorMsg/constantManage/constantDischargeDevice' //--
//import constantInsulationMonitoring from '@/components/applicationPlatform/collectorMsg/constantManage/constantInsulationMonitoring' //--
import constantSampleValue from '@/components/applicationPlatform/collectorMsg/constantManage/constantSampleValue' //--
import constantOutputValue from '@/components/applicationPlatform/collectorMsg/constantManage/constantOutputValue' //--


import peripheralControl from '@/components/applicationPlatform/collectorMsg/peripheralControl' //

Vue.use(Router)

export default new Router({
	mode:'hash',
	routes: [
    	{
    		path: '/', 
    		redirect: '/myapp/systemManage/userManage'
		},
		{ //
			path: '/system',
			name: 'system',
			component: system,
			children: [{ //
				path: 'login',
				name: 'login',
				component: login,
				children: [],
			}, ]
		}, {
			path: '/myapp',
			name: 'myApp',
			component: myApp,
			children: [{ //
				path: 'systemManage',
				name: 'systemManage',
				component: systemManage,
				children: [{ //
					path: 'userManage',
					name: 'userManage',
					component: userManage,
					children: []
				}, { //
					path: 'sysManage',
					name: 'sysManage',
					component: sysManage,
					children: []
				}, { //
					path: 'operateLog',
					name: 'operateLog',
					component: operateLog,
					children: [{ //--
						path: 'operateLogOptLog',
						name: 'operateLogOptLog',
						component: operateLogOptLog,
						children: []
					}, { //--
						path: 'operateLogCtrlLog',
						name: 'operateLogCtrlLog',
						component: operateLogCtrlLog,
						children: []
					}]
				}, ]
			}, { //
				path: 'statisticalStatement',
				name: 'statisticalStatement',
				component: statisticalStatement,
				children: [{ //
					path: 'batteryMsg',
					name: 'batteryMsg',
					component: batteryMsg,
					children: []
				}, { //
					path: 'batteryGroupMsg',
					name: 'batteryGroupMsg',
					component: batteryGroupMsg,
					children: []
				}, { //
					path: 'problemBattery',
					name: 'problemBattery',
					component: problemBattery,
					children: []
				}, ]
			}, { //
				path: 'applicationPlatform',
				name: 'applicationPlatform',
				component: applicationPlatform, //
				children: [{
						path: 'batteryMonitorSys',
						name: 'batteryMonitorSys',
						component: batteryMonitorSys,
						children: [{
							path: 'zonesEdit',
							name: 'zonesEdit',
							component: zonesEdit,
							children: []
						}, {
							path: 'collectorsEdit/:id',
							name: 'collectorsEdit',
							component: collectorsEdit,
							children: []
						}, {
							path: 'collectorMsgContainer',
							name: 'collectorMsgContainer',
							component: collectorMsgContainer,
							children: [{
									path: 'bianyi',
									name: 'bianyi',
									component: bianyi,
									children: []
								},{
									path: 'systemDiagram',
									name: 'systemDiagram',
									component: systemDiagram,
									children: []
								},{
									path: 'fileManage',
									name: 'fileManage',
									component: fileManage,
									children: []
								}, {
									path: 'alarmAnalysis',
									name: 'alarmAnalysis',
									component: alarmAnalysis,
									children: []
								}, {
									path: 'constantManage',
									name: 'constantManage',
									component: constantManage,
									children: [
										{
											path: 'constantStringValue',
											name: 'constantStringValue',
											component: constantStringValue,
											children: []
										},{
											path: 'constantDischargeDevice',
											name: 'constantDischargeDevice',
											component: constantDischargeDevice,
											children: []	
										},{
											path: 'constantSampleValue',
											name: 'constantSampleValue',
											component: constantSampleValue,
											children: []	
										},{
											path: 'constantOutputValue',
											name: 'constantOutputValue',
											component: constantOutputValue,
											children: []	
										},
									]									  
								}, {
									path: 'dataAnalysis',
									name: 'dataAnalysis',
									component: dataAnalysis,
									children: []
								}, {
									path: 'peripheralControl',
									name: 'peripheralControl',
									component: peripheralControl,
									children: []
								}, {
									path: 'runStatus',
									name: 'runStatus',
									component: runStatus,
									children: [
										{
											path:'runStatusStringValue',
											name:'runStatusStringValue',
											component:runStatusStringValue,
											children:[],
										},{
											path:'runStatusSampleValue',
											name:'runStatusSampleValue',
											component:runStatusSampleValue,
											children:[],
										},{
											path:'runStatusPowerStatus',
											name:'runStatusPowerStatus',
											component:runStatusPowerStatus,
											children:[],
										},{
											path:'runStatusOutputValue',
											name:'runStatusOutputValue',
											component:runStatusOutputValue,
											children:[],
										},{
											path:'runStatusInsulationMonitoring',
											name:'runStatusInsulationMonitoring',
											component:runStatusInsulationMonitoring,
											children:[],
										},{
											path:'runStatusChargeMachine',
											name:'runStatusChargeMachine',
											component:runStatusChargeMachine,
											children:[],
										},
									]
								}, 
							]
						}]
					}

				]
			}, ]
		}

	]
})

const app = new Vue({
	Router
})


