<template>
	<div class="system-login">
		<h4 class="title">
			LOGIN
			<span>
				
			</span>
		</h4>
		<div class="form">
			<div class="form-group">
				<span class="loginLabel">:</span>
				<input type="text" class="form-control" 
					 placeholder="" @keyup.enter="loginConfirm"
					v-model="loginParam.username">
			</div>
			<div class="form-group">
				<span class="loginLabel">:</span>
				<input type="password" class="form-control" 
					placeholder="" @keyup.enter="loginConfirm"
					v-model="loginParam.password">
			</div>
			<button type="button" 
				class="btn btn-default" v-on:click="loginConfirm"></button>
			<button type="button" 
				class="btn btn-default" v-on:click="userMsgClear"></button>
		</div>
		<span class="version-msg">
			2018(dev)
		</span>
	</div>
</template>

<script>
/*
http://localhost:8080,http://127.0.0.1:3000
1res.header("Access-Control-Allow-Origin","*");
	typeOPTIONS,
	ok
2res.header("Access-Control-Allow-Methods","PUT,POST,GET,DELETE,OPTIONS");
	OPTIONS
3cookieaccess_tokenheaders
	headers: {
		'access_token': $.cookie('access_token')
	},
4access_token
	res.header("Access-Control-Allow-Headers", "access_token,Content-Type,Content-Length, Authorization, Accept,X-Requested-With");
5aexcelheadershttp://127.0.0.1:3000cookie3.1
	access_token


3.1cookie
	xhrFields:{
		withCredentials: true,
	},
	"Access-Control-Allow-Origin"*
	res.header("Access-Control-Allow-Credentials","true");
	cookieshttp://127.0.0.1:3000cookiecookie
	res.cookie("access_token", result[0].token, {maxAge: 1000*60*60*24*30,httpOnly: true});
	acookiehttp://127.0.0.1:3000cookie4
 */
	import jquerycookie from 'jquery.cookie'
	export default {
		name: 'login',
		data() {
			return {
				loginParam:{username:'',password:''},
			}
		},
		methods: {
			loginConfirm:function(){
				var that=this;
				this.yxRequest(
					'post',
					"/sys/login",
					this.loginParam,
					function(result) {
						console.log('cookie')
						that.yxToastr.success('');
						//
						that.$store.commit('fileListRootUrl', {
							url:result.data.UserName,
						})
						that.setLocalStorage('userInfo',JSON.stringify(result.data));
						
						
						$.cookie("access_token",result.data.token,{  expires :7 ,path :' / '});
						setTimeout(function(){
							that.$router.push({path:'/myapp/systemManage/userManage'})
						},2000)
					},
					function(result) {
						that.yxToastr.error(':'+result.msg);
						console.log('err:',result);
				})
			},
			userMsgClear:function(){
				this.loginParam={username:'',password:''};
			},
		},
	}
</script>

<style lang="less" scoped>
@import '../../style/app.less';
.system-login{
	width: 50%;
	min-width:550px;
	margin: auto;
	height: 50%;
    margin-top: 10%;
	background: @active-color;
	position:relative;
	.title{
	    text-align: left;
	    padding: 8px;
	    font-size: 84px;
	    font-weight: 900;
	    color: lighten(@active-color,30);
		>span{
		    display: inline-block;
		    font-size: 16px;
		    color: #fff;
		    margin-left: 110px;
		}
	}
	>.form{
		width: 80%;
	    margin: auto;
	    padding: 12px 0;
	    background: lighten(@active-color,50%);
	}
	.form-group{
		
		>.loginLabel{
			display: inline-block;
			width: 60px;
		}
		input{
			display:inline-block;
		    width: 40%;
		}
	}
	.version-msg{
		position: absolute;
    	bottom: -30px;
    	color:@active-color;
	}
}
</style>


