<template>
<div class="applicationPlatform-collectorEdit">
	<div class="panel panel-default">
		<div class="panel-heading">
			<button type="button" class="btn btn-primary btn-sm"
				v-on:click="urlGo('/myapp/applicationPlatform/batteryMonitorSys/zonesEdit')">
				<i class="glyphicon glyphicon-chevron-left"></i>
				
			</button>
			<h3 class="panel-title">{{$store.state.currentZone?$store.state.currentZone.Zone:''}}</h3>
		</div>
		<div class="panel-body">
			<button class="btn btn-default" v-on:click="stationAddFun"></button>
			<div class="table-responsive">
				<table class="table table-bordered table-hover">
					<thead>
						<tr>
							<th></th>
							<th></th>
							<th></th>
							<th>ip</th>
							<th></th>
							<th>Modbus</th>
							<th></th>
							<th></th>
							<th>ID</th>
							<th></th>
						</tr>
					</thead>
					<tbody>
						<tr v-for="item in stationList">
							<td>{{item.StationID}}</td>
							<td>{{item.StationName}}</td>
							<td>
								<span v-for="type in $store.state.stationTypeList" 
									v-if="type.StationTypeID==item.StationType">
									{{type.StationTypeName}}
								</span>    
							</td>
							<td>{{item.IPAddress}}</td>
							<td>{{item.Port}}</td>
							<td>{{item.ModbusAddress}}</td>
							<td>{{item.IPMask}}</td>
							<td>{{item.Gateway}}</td>
							<td>{{item.PowerType}}</td>
							<td>
								<a class="hot-word twoWordWidth" v-on:click="stationEditFun(item)"></a>
								<a class="danger-word twoWordWidth" v-on:click="stationDeleteFun(item.StationID)"></a>
								<a class="hot-word twoWordWidth" v-on:click="getStation(item)"></a>
							</td>
						</tr>
					</tbody>
				</table>
			</div>
		</div>
		<div class="panel-footer"></div>
	</div>
	<div class="modal fade stationEditModal" tabindex="-1" 
		role="dialog" aria-labelledby="mySmallModalLabel">
		<div class="modal-dialog" role="document">
			<div class="modal-content">
				<div class="modal-header">
					
				</div>
				<div class="modal-body">
					<div class="form-horizontal">
						<div class="form-group">
							<label for="form_stationname" 
								class="col-sm-4 control-label">
								:
							</label>
							<div class="col-sm-8">
								<input type="text" class="form-control" id="form_stationname" 
									placeholder="" v-model="stationObj.StationName">
							</div>
						</div>
						<div class="form-group">
							<label for="form_StationType" class="col-sm-4 control-label">:</label>
							<div class="col-sm-8">
								<select class="form-control" id="form_StationType" v-model="stationObj.StationType">
									<option v-for="type in $store.state.stationTypeList" :value="type.StationTypeID">{{type.StationTypeName}}</option>
								</select>
							</div>
						</div>
						<div class="form-group">
							<label for="form_IPAddress" class="col-sm-4 control-label">ip:</label>
							<div class="col-sm-8">
								<input type="tel" class="form-control" id="form_IPAddress" 
									placeholder="ip" v-model="stationObj.IPAddress">
							</div>
						</div>
						<div class="form-group">
							<label for="form_Port" class="col-sm-4 control-label">:</label>
							<div class="col-sm-8">
								<input type="tel" class="form-control" id="form_Port" 
									placeholder="" v-model="stationObj.Port">
							</div>
						</div>
						
						<div class="form-group">
							<label for="form_ModbusAddress" class="col-sm-4 control-label">Modbus:</label>
							<div class="col-sm-8">
								<input type="tel" class="form-control" id="form_ModbusAddress" 
									placeholder="Modbus" v-model="stationObj.ModbusAddress">
							</div>
						</div>
						<div class="form-group">
							<label for="form_IPMask" class="col-sm-4 control-label">:</label>
							<div class="col-sm-8">
								<input type="tel" class="form-control" id="form_IPMask" 
									placeholder="" v-model="stationObj.IPMask">
							</div>
						</div>
						<div class="form-group">
							<label for="form_Gateway" class="col-sm-4 control-label">:</label>
							<div class="col-sm-8">
								<input type="tel" class="form-control" id="form_Gateway" 
									placeholder="" v-model="stationObj.Gateway">
							</div>
						</div>
						<div class="form-group">
							<label for="form_PowerType" class="col-sm-4 control-label">ID:</label>
							<div class="col-sm-8">
								<input type="tel" class="form-control" id="form_PowerType" 
									placeholder="ID" v-model="stationObj.PowerType">
							</div>
						</div>
						<div class="form-group">
							<label for="form_Longitude" class="col-sm-4 control-label">:</label>
							<div class="col-sm-8">
								<input type="tel" class="form-control" id="form_Longitude" 
									placeholder="" v-model="stationObj.Longitude">
							</div>
						</div>
						<div class="form-group">
							<label for="form_Latitude" class="col-sm-4 control-label">:</label>
							<div class="col-sm-8">
								<input type="tel" class="form-control" id="form_Latitude" 
									placeholder="" v-model="stationObj.Latitude">
							</div>
						</div>
					</div>
				</div>
				<div class="modal-footer">
					<button type="button" class="btn yx-btn-active" v-on:click="stationEditConfirm"></button>
					<button type="button" class="btn yx-btn-cancel" data-dismiss="modal"></button>
				</div>
			</div>
		</div>
	</div>
	<div class="modal fade stationDeleteConfirmModal" tabindex="-1" 
		role="dialog" aria-labelledby="mySmallModalLabel">
		<div class="modal-dialog modal-sm" role="document">
			<div class="modal-content">
				<div class="modal-header">
					
				</div>
				<div class="modal-body">
					<div class="form-horizontal">
						
					</div>
				</div>
				<div class="modal-footer">
					<button type="button" class="btn btn-primary" v-on:click="stationDeleteConfirm(deleteID)"></button>
					<button type="button" class="btn btn-danger" data-dismiss="modal"></button>
				</div>
			</div>
		</div>
	</div>
</div>
</template>

<script>

export default {
	name: 'collectorsEdit',
	data() {
		return {
			stationList:[],
			stationObj:{},
		}
	},
	watch: {},
	methods: {
		getCurrentZone:function(id){
			var that=this;
			this.yxRequest(
				'get',
				"/platform/currentzone", 
				{id:id},
				function(result) {
					//console.log(result);
					that.$store.commit('getCurrentZone', {
						Zone:result.data[0]
					})
					that.getStationList(result.data[0].id);
				},
				function(result) {
					that.yxToastr.error('currentzone:'+result.msg);
					console.log('err:',result);
			})
		},
		getStationList:function(ZoneID){
			var that=this;
			this.yxRequest(
				'get',
				"/platform/stationlist", 
				{ZoneID:ZoneID},
				function(result) {
					that.stationList=result.data;
				},
				function(result) {
					that.yxToastr.error(':'+result.msg);
					console.log('err:',result);
			})
		},
		stationAddFun:function(){
			this.stationObj={
				ZoneID:this.$route.params.id,
				StationName:"",StationType:"",
				IPAddress:"",Port:"",
				ModbusAddress:"",IPMask:"",
				Gateway:"",PowerType:"",
				Longitude:"",Latitude:""
			}
			$('.stationEditModal').modal('show');
		},
		stationEditFun:function(station){
			this.stationObj=this.mycopy(station);
			$('.stationEditModal').modal('show');
		},
		stationEditConfirm:function(){
			console.log(this.stationObj)
			for(let key in this.stationObj){
				if(!this.stationObj[key]){
					this.yxToastr.warning('');
					return ;
				}
			}
			
			let that=this;
			if(this.stationObj.StationID){
				this.yxRequest(
					'post',
					"/platform/stationedit", 
					this.stationObj,
					function(result) {
						console.log(result);
						that.yxToastr.success('');
						that.getStationList(that.$store.state.currentZone.id);
						$('.stationEditModal').modal('hide');
					},
					function(result) {
						that.yxToastr.error(':'+result.msg);
						console.log('err:',result);
				})
			}else{
				this.yxRequest(
					'post',
					"/platform/stationadd", 
					this.stationObj,
					function(result) {
						that.yxToastr.success('');
						that.getStationList(that.$store.state.currentZone.id);
						$('.stationEditModal').modal('hide');
						console.log(result);
					},
					function(result) {
						that.yxToastr.error(':'+result.msg);
						console.log('err:',result);
				})
			}
		},
		stationDeleteFun:function(stationID){
			console.log(stationID);
			this.deleteID=stationID;
			$('.stationDeleteConfirmModal').modal('show');
		},
		stationDeleteConfirm:function(StationID){
			let that=this;
			this.yxRequest(
				'post',
				"/platform/stationdelete", 
				{StationID:StationID},
				function(result) {
					that.yxToastr.success('');
					that.getStationList(that.$store.state.currentZone.id);
					$('.stationDeleteConfirmModal').modal('hide');
				},
				function(result) {
					that.yxToastr.error(':'+result.msg);
					console.log('err:',result);
			})
		},
		getStation:function(item){
			this.$store.commit('getCurrentStation', {
				station:item
			})
			this.setLocalStorage('currentStationID',item.StationID);
			//this.$router.push(`/myapp/applicationPlatform/batteryMonitorSys/collectorMsgContainer/systemDiagram/${item.StationID}`)
			this.$router.push(`/myapp/applicationPlatform/batteryMonitorSys/collectorMsgContainer/systemDiagram`)
		}
	},
	created() {
		if(!this.$store.state.currentZone){
			console.log('currentZone')
			this.getCurrentZone(this.$route.params.id);
		}else{
			this.getStationList(this.$store.state.currentZone.id);
		}
		
		
		console.log(this.$store.state.stationTypeList)
	},
	components:{}
}
</script>
<style lang="less" scoped>
@import '../../style/app.less';

.applicationPlatform-collectorEdit{
	.panel-body{
		>.btn{
			float: right;
		}
		>.table-responsive{
			margin-top: 40px;
		}
	}
	.modal-dialog{
		.modal-dialog(450px);
	}
}	
</style>


